<template>
  <div class="login" clearfix>
    <div class="login-wrap">
      <el-row>
        <el-col>
          <h2>登陆</h2>
        </el-col>
      </el-row>
      <el-form ref="loginForm" :model="loginForm" :rules="rules" label-position="left" label-width="auto">
        <el-row type="flex" justify="center">
          <el-col :span="16">
            <el-form-item label="用户名" prop="username">
              <el-input type="text" v-model="loginForm.username" placeholder="请输入用户名"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" justify="center">
          <el-col :span="16">
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" justify="center">
          <el-col :span="16">
            <el-form-item label="类型">
              <el-radio-group v-model="loginForm.type">
                <el-radio-button label="admin">管理员</el-radio-button>
                <el-radio-button label="teacher">老师</el-radio-button>
                <el-radio-button label="student">学生</el-radio-button>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" justify="center">
          <el-col :span="16">
            <el-form-item label="验证码" prop="verifyCode">
              <el-input type="text" v-model="loginForm.verifyCode" placeholder="- - - -" @keyup.enter.native="submitForm('loginForm')">
                <template slot="append">
                  <el-image :src="src" @click="refreshCode" style="width: 80px; height: 33px"></el-image>
                </template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="primary" @click="submitForm('loginForm')">提交</el-button>
          <el-button type="danger" @click="resetForm('loginForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
        type: "admin",
        verifyCode: ""
      },
      rules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        type: [
          {required: true, message: '请输入账户类型', trigger: 'change'}
        ],
        verifyCode: [
          {required: true, message: '请输入验证码', trigger: 'blur'},
          {min: 4, max: 4, message: '长度为 4 个字符', trigger: 'blur'}
        ],
      },
      src: "",
      verifyKey: "",
      errorMsg: ""
    };
  },
  methods: {
    refreshCode() {
      this.$http.get("http://localhost:8989/sys/createVerifyCodeImg").then(data => {
        this.verifyKey = data.data.key;
        const num = Math.ceil(Math.random() * 10);    //生成随机数，防止缓存
        this.src = "http://localhost:8989/sys/verifyCodeImg?" + num;
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post("http://localhost:8989/sys/login/" + this.verifyKey, this.loginForm).then(data => {
            if (!data.data.success) {
              this.errorMsg = data.data.msg;
              this.warningMsg();
              this.refreshCode();
            } else {
              localStorage.setItem("token", data.data.token);
              localStorage.setItem("portrait", data.data.portraitPath);
              localStorage.setItem("id", data.data.id);
              localStorage.setItem("role", this.loginForm.type);
              localStorage.setItem("name", this.loginForm.username);
              this.$router.push({
                name: 'Dashboard'
              })
            }
          })
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.loginForm.type = "admin";
      this.errorMsg = "";
    },
    warningMsg(){
      this.$message({
        showClose: true,
        message: this.errorMsg,
        type: 'error'
      });
    }
  },
  created() {
    this.refreshCode();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  width: 100%;
  height: 100%;
  background: url("../assets/images/bg1.jpg") no-repeat;
  background-size: cover;
  overflow: hidden;
}

.login-wrap {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: rgba(200, 200, 200, 0.5);
  background-size: cover;
  width: 450px;
  height: 420px;
  margin: 215px auto;
  border: white 1px solid;
  overflow: hidden;
  padding-top: 10px;
  line-height: 40px;
}
</style>



