<template>
  <div>
    <el-container>
      <el-header style="font-size: 12px" height="40px">

        <el-row :gutter="10" type="flex" align="middle">
          <el-col :span="2">
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              size="medium"
              @click="addNewAdminInfo"
            >添加
            </el-button>
          </el-col>

        </el-row>

      </el-header>

      <el-main style="height: 725px; width: 900px">
        <!--        展示表格-->
        <el-table :data="adminList" :stripe="true" :border="true">
          <el-table-column prop="name" label="姓名" width="120">
          </el-table-column>
          <el-table-column prop="gender" label="性别" width="120">
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="180">
          </el-table-column>
          <el-table-column prop="telephone" label="电话" width="120">
          </el-table-column>
          <el-table-column prop="address" label="地址" width="120">
          </el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editAdminInfo(scope.row)"
              >编辑
              </el-button>

              <el-popconfirm
                title="确定删除该条记录吗？"
                confirm-button-type="danger"
                @confirm="handleDelete(scope.row)"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  slot="reference"
                >删除
                </el-button>
              </el-popconfirm>

            </template>
          </el-table-column>
        </el-table>
      </el-main>

      <el-footer height="15px">
        <el-row type="flex" align="bottom" justify="center">
          <el-pagination
            background
            @size-change="selectAdminListByPage"
            @current-change="selectAdminListByPage"
            :page-count="5"
            :current-page.sync='curPage'
            :page-sizes="[5, 10, 12]"
            :page-size.sync='pageSize'
            layout="total, sizes, prev, pager, next, jumper"
            :total=this.totalNum>
          </el-pagination>
        </el-row>
      </el-footer>

    </el-container>
    <!--    添加或修改表单-->
    <el-dialog :title="dialogTitle" :visible.sync="dialogTableVisible" @closed="resetAdminInfo">
      <el-form :model="adminInfo" :rules="rules" ref="adminInfo" label-width="60px" size="medium">
        <el-row :gutter="1">
          <el-col :span="10" :offset="1">

            <el-row>
              <el-col>
                <el-form-item label="姓名" prop="name">
                  <el-input v-model="adminInfo.name" placeholder="请输入姓名"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="性别" prop="gender">
                  <el-radio v-model="adminInfo.gender" label="男">
                    男
                  </el-radio>
                  <el-radio v-model="adminInfo.gender" label="女">
                    女
                  </el-radio>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="电话" prop="telephone">
                  <el-input v-model="adminInfo.telephone" placeholder="请输入电话"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="邮箱" prop="email">
                  <el-input v-model="adminInfo.email" placeholder="请输入邮箱"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="地址" prop="address">
                  <el-input v-model="adminInfo.address" placeholder="请输入地址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-col>

          <!--          上传头像-->
          <el-col :span="10" :offset="3">
            <el-form-item label="头像" prop="portraitPath">
              <el-upload
                ref="uploadPortrait"
                action="http://localhost:8989/sys/upload"
                :headers="headers"
                :multiple="false"
                :limit="1"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                list-type="picture"
                accept=".jpeg, .JPEG, .jpg, .JPG, .png, .PNG"
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
              </el-upload>
            </el-form-item>
          </el-col>

        </el-row>
        <el-row type="flex" justify="center">
          <el-form-item>
            <el-button type="primary" @click="submitForm('adminInfo')">提交</el-button>
            <el-button @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "AdminList",
  data() {
    return {
      headers: {
        token: localStorage.getItem("token")
      },
      id: localStorage.getItem("id"),
      role: localStorage.getItem("role"),

      adminList: [],
      adminName: "",
      clazzList: [],
      clazzName: '',
      curPage: 1,
      totalNum: 0,
      pageSize: 10,
      dialogTableVisible: false,
      dialogTitle: '',
      adminInfo: {
        name: '',
        gender: '男',
        email: '',
        telephone: '',
        address: '',
        introduction: '',
        portraitPath: ''
      },
      rules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid){
          if (this.dialogTitle === "添加新老师信息"){
            this.submitNewAdminInfo();
          }else if (this.dialogTitle === "编辑老师信息"){
            this.updateAdminInfo();
          }
        }else {
          return false;
        }
      })
    },

    //更新老师信息
    updateAdminInfo(){
      this.$http.post("http://localhost:8989/admin/updateInfo", this.adminInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectAdminListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //编辑老师信息
    editAdminInfo(row) {
      this.dialogTitle = "编辑老师信息";
      this.$http.get("http://localhost:8989/admin/selectById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.adminInfo = data.data.adminInfo;
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
      this.dialogTableVisible = true;
    },

    //添加老师信息
    submitNewAdminInfo(){
      this.$http.post("http://localhost:8989/admin/insert", this.adminInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectAdminListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //删除头像
    handleRemove() {
      if (this.adminInfo.portraitPath !== '' && this.adminInfo.portraitPath !== null && this.adminInfo.portraitPath.indexOf("default") === -1) {
        this.$http.delete("http://localhost:8989/sys/deletePortrait?portraitPath=" + this.adminInfo.portraitPath, {
          headers: this.headers
        }).then(data => {
          if (data.data.success) {
            this.adminInfo.portraitPath = '';
            this.$refs.uploadPortrait.clearFiles();
          } else {
            this.$message({
              message: data.data.msg,
              type: 'error'
            })
          }
        })
      }
    },
    //上传成功的钩子
    handleSuccess(response) {
      this.adminInfo.portraitPath = response.portraitPath;
    },

    //关闭表单
    closeDialog(){
      this.dialogTableVisible = false;
      this.resetAdminInfo();
      this.$refs['adminInfo'].clearValidate();
    },

    //重置表单信息
    resetAdminInfo() {
      this.dialogTitle = "";

      this.$refs.uploadPortrait.clearFiles();

      this.adminInfo = {
        name: '',
        gender: '男',
        email: '',
        telephone: '',
        address: '',
        introduction: '',
        portrait: ''
      }
    },

    //添加新老师
    addNewAdminInfo() {
      this.dialogTitle = "添加新老师信息";
      this.dialogTableVisible = true;
    },

    //老师分页查询
    selectAdminListByPage() {
      this.$http.get("http://localhost:8989/admin/selectAdminListByPage/" + this.curPage + "/" + this.pageSize, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.totalNum = data.data.totalNum;
          this.adminList = data.data.adminList;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //获取班级列表
    selectClassList() {
      this.$http.get("http://localhost:8989/clazz/getClazzNames", {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.clazzList = data.data.clazzNames;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //删除老师信息
    handleDelete(row) {
      console.log(row);
      this.$http.delete("http://localhost:8989/admin/deleteById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          })
          this.selectAdminListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    }
  },
  created() {
    this.selectClassList();
    this.selectAdminListByPage();
  }
}
</script>

<style scoped>
</style>



