<template>
  <div>
    <el-container>
      <el-header style="font-size: 12px" height="40px">

        <el-row :gutter="10" type="flex" align="middle"  v-if="['admin'].includes(this.role)">
          <el-col :span="2">
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              size="medium"
              @click="addNewGradeInfo"
            >添加
            </el-button>
          </el-col>
        </el-row>

      </el-header>

      <el-main style="height: 725px; width: 1020px">
        <!--        展示表格-->
        <el-table :data="gradeList" :stripe="true" :border="true">
          <el-table-column prop="name" label="年级" width="120">
          </el-table-column>
          <el-table-column prop="manager" label="主任" width="140">
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="180">
          </el-table-column>
          <el-table-column prop="introduction" width="340" label="介绍">
          </el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editGradeInfo(scope.row)"
                :disabled="available()"
              >编辑
              </el-button>

              <el-popconfirm
                title="确定删除该条记录吗？"
                confirm-button-type="danger"
                @confirm="handleDelete(scope.row)"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  slot="reference"
                  :disabled="available()"
                >删除
                </el-button>
              </el-popconfirm>

            </template>
          </el-table-column>
        </el-table>
      </el-main>

      <el-footer height="15px">
        <el-row type="flex" align="bottom" justify="center">
          <el-pagination
            background
            @size-change="selectGradeListByPage"
            @current-change="selectGradeListByPage"
            :page-count="5"
            :current-page.sync='curPage'
            :page-sizes="[5, 10, 12]"
            :page-size.sync='pageSize'
            layout="total, sizes, prev, pager, next, jumper"
            :total=this.totalNum>
          </el-pagination>
        </el-row>
      </el-footer>

    </el-container>
    <!--    添加或修改表单-->
    <el-dialog :title="dialogTitle" :visible.sync="dialogTableVisible" @closed="resetGradeInfo" width="30%">
      <el-form :model="gradeInfo" :rules="rules" ref="gradeInfo" label-width="60px" size="medium">
        <el-row>
          <el-col>

            <el-row>
              <el-col>
                <el-form-item label="年级" prop="name">
                  <el-input v-model="gradeInfo.name" placeholder="请输入年级"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="主任" prop="gradeName">
                  <el-select v-model="gradeInfo.manager" clearable>
                    <el-option
                      v-for="teacher in teacherList"
                      :key="teacher.id"
                      :label="teacher.name"
                      :value="teacher.name"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="邮箱" prop="email">
                  <el-input v-model="gradeInfo.email" placeholder="请输入邮箱"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="电话" prop="telephone">
                  <el-input v-model="gradeInfo.telephone" placeholder="请输入电话"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="介绍" prop="introduction">
                  <el-input v-model="gradeInfo.introduction" placeholder="请输入年级介绍" type="textarea"
                            :rows="3"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-col>

        </el-row>
        <el-row type="flex" justify="center">
          <el-form-item>
            <el-button type="primary" @click="submitForm('gradeInfo')">提交</el-button>
            <el-button @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "GradeList",
  data() {
    return {
      headers: {
        token: localStorage.getItem("token")
      },
      id: localStorage.getItem("id"),
      role: localStorage.getItem("role"),


      gradeName: "",
      gradeList: [],
      teacherList: [],
      curPage: 1,
      totalNum: 0,
      pageSize: 10,
      dialogTableVisible: false,
      dialogTitle: '',
      gradeInfo: {
        name: '',
        manager: '',
        email: '',
        telephone: '',
        introduction: '',
      },
      rules: {
        name: [
          {required: true, message: '请输入年级名', trigger: 'blur'}
        ],
        manager: [
          {required: true, message: '请选择主任', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    //授权
    available(){
      if(['admin'].includes(this.role)){
        return false;
      }
      return true;
    },

    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogTitle === "添加新年级信息") {
            this.submitNewGradeInfo();
          } else if (this.dialogTitle === "编辑年级信息") {
            this.updateGradeInfo();
          }
        } else {
          return false;
        }
      })
    },

    //更新年级信息
    updateGradeInfo() {
      this.$http.post("http://localhost:8989/grade/updateInfo", this.gradeInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectGradeListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //编辑年级信息
    editGradeInfo(row) {
      this.dialogTitle = "编辑年级信息";
      this.$http.get("http://localhost:8989/grade/selectById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.gradeInfo = data.data.gradeInfo;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
      this.dialogTableVisible = true;
    },

    //添加年级信息
    submitNewGradeInfo() {
      this.$http.post("http://localhost:8989/grade/insert", this.gradeInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectGradeListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //关闭表单
    closeDialog() {
      this.dialogTableVisible = false;
      this.resetGradeInfo();
      this.$refs['gradeInfo'].clearValidate();
    },

    //重置表单信息
    resetGradeInfo() {
      this.dialogTitle = "";

      this.gradeInfo = {
        name: '',
        manager: '',
        email: '',
        telephone: '',
        introduction: '',
      }
    },

    //添加新年级
    addNewGradeInfo() {
      this.dialogTitle = "添加新年级信息";
      this.dialogTableVisible = true;
    },

    //年级分页查询
    selectGradeListByPage() {
      this.$http.get("http://localhost:8989/grade/selectGradeListByPage/" + this.curPage + "/" + this.pageSize, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.totalNum = data.data.totalNum;
          this.gradeList = data.data.gradeList;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //获取班主任列表
    selectTeacherList() {
      this.$http.get("http://localhost:8989/teacher/getTeacherNames", {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.teacherList = data.data.teacherNames;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //删除年级信息
    handleDelete(row) {
      this.$http.delete("http://localhost:8989/grade/deleteById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          })
          this.selectGradeListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    }
  },
  created() {
    this.selectTeacherList();
    this.selectGradeListByPage();
  }
}
</script>

<style scoped>
</style>



