<template>
  <div>
    <el-container>
      <el-header style="font-size: 12px" height="40px">

        <el-row :gutter="10" type="flex" align="middle">
          <el-col :span="2" v-if="['teacher', 'admin'].includes(this.role)">
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              size="medium"
              @click="addNewStudentInfo"
            >添加
            </el-button>
          </el-col>

          <el-col :span="3">
            <el-select v-model="clazzName" clearable placeholder="请选择班级" size="medium"
                       @change="changePage">
              <el-option
                v-for="clazz in clazzList"
                :key="clazz.id"
                :label="clazz.name"
                :value="clazz.name">
              </el-option>
            </el-select>
          </el-col>

          <el-col :span="3">
            <el-input
              placeholder="请输入姓名"
              v-model="studentName"
              size="medium"
              clearable
              @change="changePage"
              @keypress.enter.native="filtrateStudent"
            >
            </el-input>
          </el-col>

          <el-col :span="2">
            <el-button
              icon="el-icon-search"
              size="medium"
              @click="filtrateStudent"
            >搜索
            </el-button>
          </el-col>

        </el-row>

      </el-header>

      <el-main style="height: 725px">
        <!--        展示表格-->
        <el-table :data="studentList" :stripe="true" :border="true">
          <el-table-column prop="sno" label="学号" width="140">
          </el-table-column>
          <el-table-column prop="name" label="姓名" width="120">
          </el-table-column>
          <el-table-column prop="gender" label="性别" width="120">
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="180">
          </el-table-column>
          <el-table-column prop="telephone" label="电话" width="120">
          </el-table-column>
          <el-table-column prop="introduction" label="介绍">
          </el-table-column>
          <el-table-column prop="address" label="地址" width="120">
          </el-table-column>
          <el-table-column prop="clazzName" label="班级" width="120">
          </el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editStudentInfo(scope.row)"
                :disabled="editAvailable(scope.row)"
              >编辑
              </el-button>

              <el-popconfirm
                title="确定删除该条记录吗？"
                confirm-button-type="danger"
                @confirm="handleDelete(scope.row)"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  slot="reference"
                  :disabled="deleteAvailable()"
                >删除
                </el-button>
              </el-popconfirm>

            </template>
          </el-table-column>
        </el-table>
      </el-main>

      <el-footer height="15px">
        <el-row type="flex" align="bottom" justify="center">
          <el-pagination
            background
            @size-change="changePage"
            @current-change="changePage"
            :page-count="5"
            :current-page.sync='curPage'
            :page-sizes="[5, 10, 12]"
            :page-size.sync='pageSize'
            layout="total, sizes, prev, pager, next, jumper"
            :total=this.totalNum>
          </el-pagination>
        </el-row>
      </el-footer>

    </el-container>
    <!--    添加或修改表单-->
    <el-dialog :title="dialogTitle" :visible.sync="dialogTableVisible" @closed="resetStudentInfo">
      <el-form :model="studentInfo" :rules="rules" ref="studentInfo" label-width="60px" size="medium">
        <el-row :gutter="1">
          <el-col :span="10" :offset="1">
            <el-row>
              <el-col>
                <el-form-item label="班级" prop="clazzName">
                  <el-select v-model="studentInfo.clazzName" clearable>
                    <el-option
                      v-for="clazz in clazzList"
                      :key="clazz.id"
                      :label="clazz.name"
                      :value="clazz.name"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="姓名" prop="name">
                  <el-input v-model="studentInfo.name" placeholder="请输入姓名"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="性别" prop="gender">
                  <el-radio v-model="studentInfo.gender" label="男">
                    男
                  </el-radio>
                  <el-radio v-model="studentInfo.gender" label="女">
                    女
                  </el-radio>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="学号" prop="sno">
                  <el-input v-model="studentInfo.sno" placeholder="请输入学号"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="邮箱" prop="email">
                  <el-input v-model="studentInfo.email" placeholder="请输入邮箱"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="地址" prop="address">
                  <el-input v-model="studentInfo.address" placeholder="请输入地址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="介绍" prop="introduction">
                  <el-input v-model="studentInfo.introduction" placeholder="请输入学生介绍" type="textarea"
                            :rows="3"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-col>

          <!--          上传头像-->
          <el-col :span="10" :offset="3">
            <el-form-item label="头像" prop="portraitPath">
              <el-upload
                ref="uploadPortrait"
                action="http://localhost:8989/sys/upload"
                :headers="headers"
                :multiple="false"
                :limit="1"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                list-type="picture"
                accept=".jpeg, .JPEG, .jpg, .JPG, .png, .PNG"
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
              </el-upload>
            </el-form-item>
          </el-col>

        </el-row>
        <el-row type="flex" justify="center">
          <el-form-item>
            <el-button type="primary" @click="submitForm('studentInfo')">提交</el-button>
            <el-button @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "StudentList",
  data() {
    return {
      headers: {
        token: localStorage.getItem("token")
      },
      id: localStorage.getItem("id"),
      role: localStorage.getItem("role"),

      studentList: [],
      studentName: "",
      clazzList: [],
      clazzName: '',
      curPage: 1,
      totalNum: 0,
      pageSize: 10,
      dialogTableVisible: false,
      dialogTitle: '',
      studentInfo: {
        clazzName: '',
        name: '',
        gender: '男',
        sno: '',
        email: '',
        telephone: '',
        address: '',
        portraitPath: ''
      },
      rules: {
        clazzName: [
          {required: true, message: '请选择班级', trigger: 'blur'}
        ],
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'}
        ],
        gender: [
          {required: true, message: '请选择性别', trigger: 'blur'}
        ],
        sno: [
          {required: true, message: '请输入学号', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    //授权编辑操作
    editAvailable(row){
      if (['teacher', 'admin'].includes(this.role)){
        return false;
      }else {
        if(row.id != this.id){
          return true;
        }
      }
      return false;
    },
    deleteAvailable(){
      if (['teacher', 'admin'].includes(this.role)){
        return false;
      }
      return true;
    },

    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid){
          if (this.dialogTitle === "添加新学生信息"){
            this.submitNewStudentInfo();
          }else if (this.dialogTitle === "编辑学生信息"){
            this.updateStudentInfo();
          }
        }else {
          return false;
        }
      })
    },

    //更新学生信息
    updateStudentInfo(){
      this.$http.post("http://localhost:8989/student/updateInfo", this.studentInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectStudentListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //编辑学生信息
    editStudentInfo(row) {
      this.dialogTitle = "编辑学生信息";
      this.$http.get("http://localhost:8989/student/selectById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.studentInfo = data.data.studentInfo;
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
      this.dialogTableVisible = true;
    },

    //添加学生信息
    submitNewStudentInfo(){
      this.$http.post("http://localhost:8989/student/insert", this.studentInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectStudentListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //删除头像
    handleRemove() {
      if (this.studentInfo.portraitPath !== '' && this.studentInfo.portraitPath !== null && this.studentInfo.portraitPath.indexOf("default") === -1) {
        this.$http.delete("http://localhost:8989/sys/deletePortrait?portraitPath=" + this.studentInfo.portraitPath, {
          headers: this.headers
        }).then(data => {
          if (data.data.success) {
            this.studentInfo.portraitPath = '';
            this.$refs.uploadPortrait.clearFiles();
          } else {
            this.$message({
              message: data.data.msg,
              type: 'error'
            })
          }
        })
      }
    },
    //上传成功的钩子
    handleSuccess(response) {
      this.studentInfo.portraitPath = response.portraitPath;
    },

    //关闭表单
    closeDialog(){
      this.dialogTableVisible = false;
      this.resetStudentInfo();
      this.$refs['studentInfo'].clearValidate();
    },

    //重置表单信息
    resetStudentInfo() {
      this.dialogTitle = "";

      this.$refs.uploadPortrait.clearFiles();

      this.studentInfo = {
        clazzName: '',
        name: '',
        gender: '男',
        sno: '',
        email: '',
        telephone: '',
        address: '',
        portrait: ''
      }
    },

    //添加新学生
    addNewStudentInfo() {
      this.dialogTitle = "添加新学生信息";
      this.dialogTableVisible = true;
    },

    changePage(){
      if (this.studentName !== '' || this.clazzName !== ''){
        this.filtrateStudent();
      }else {
        this.selectStudentListByPage();
      }
    },

    //学生分页查询
    selectStudentListByPage() {
      this.$http.get("http://localhost:8989/student/selectStudentListByPage/" + this.curPage + "/" + this.pageSize, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.totalNum = data.data.totalNum;
          this.studentList = data.data.studentList;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //获取班级列表
    selectClassList() {
      this.$http.get("http://localhost:8989/clazz/getClazzNames", {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.clazzList = data.data.clazzNames;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //对学生按班级姓名查询
    filtrateStudent() {
      if (this.studentName !== '' || this.clazzName !== '') {
        this.$http.get("http://localhost:8989/student/filtrateStudentListByPage/" + this.curPage + "/" + this.pageSize + "?studentName=" + this.studentName + "&clazzName=" + this.clazzName, {
          headers: this.headers
        }).then(data => {
          if (data.data.success) {
            this.studentList = data.data.studentList;
            this.totalNum = data.data.totalNum;
          } else {
            this.$message({
              message: data.data.msg,
              type: 'error'
            })
          }
        })
      } else {
        this.selectStudentListByPage();
      }
    },

    //删除学生信息
    handleDelete(row) {
      this.$http.delete("http://localhost:8989/student/deleteById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          })
          this.selectStudentListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    }
  },
  created() {
    this.selectClassList();
    this.selectStudentListByPage();
  }
}
</script>

<style scoped>
</style>



