<template>
  <div>
    <el-container>
      <el-header class="header" height="40px" style="background-color: aquamarine;">

        <el-row type="flex" style="height: 100%" align="middle" :gutter="2">

          <el-col :span="2" style="font-family: Microsoft YaHei,serif; font-size: 22px">
            学生管理系统
          </el-col>

          <el-col :span="0.5" :offset="19">
            <span>
              <el-avatar size="small" :src="this.portrait" @error="errorHandler"></el-avatar>
            </span>
          </el-col>

          <el-col :span="2">
            <span>
              欢迎，{{ this.name }}！
            </span>
          </el-col>

          <el-col :span="0.5">
            <el-dropdown @command="handleCommand" trigger="click">
                <span>
                <i class="el-icon-setting" style="font-size: 20px"></i>
                </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="changePortrait">修改头像</el-dropdown-item>
                <el-dropdown-item command="changePassword">修改密码</el-dropdown-item>
                <el-dropdown-item command="quit">退出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
      </el-header>
      <el-container>
        <el-aside width="180px" style="background-color: blue">
          <el-menu>
            <el-submenu index="1" v-if="['student', 'teacher', 'admin'].includes(this.role)">
              <template slot="title"><i class="el-icon-user"></i>学生信息管理</template>
              <el-menu-item-group>
                <el-menu-item index="1-1" @click="switchView('StudentList')">学生列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2" v-if="['student', 'teacher', 'admin'].includes(this.role)">
              <template slot="title"><i class="el-icon-user-solid"></i>教师信息管理</template>
              <el-menu-item-group>
                <el-menu-item index="2-1" @click="switchView('TeacherList')">教师列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3" v-if="['student', 'teacher', 'admin'].includes(this.role)">
              <template slot="title"><i class="el-icon-school"></i>班级信息管理</template>
              <el-menu-item-group>
                <el-menu-item index="3-1" @click="switchView('ClassList')">班级列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4" v-if="['student', 'teacher', 'admin'].includes(this.role)">
              <template slot="title"><i class="el-icon-office-building"></i>年级信息管理</template>
              <el-menu-item-group>
                <el-menu-item index="4-1" @click="switchView('GradeList')">年级列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="5" v-if="['admin'].includes(this.role)">
              <template slot="title"><i class="el-icon-s-custom"></i>系统用户管理</template>
              <el-menu-item-group>
                <el-menu-item index="5-1" @click="switchView('AdminList')">管理员列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>

        <el-main style="height: 820px;">
          <div :is="currentView">
          </div>
        </el-main>

      </el-container>

      <el-footer style="background-color: red; height: 55px;">
        <el-row type="flex" justify="center" align="middle" style="font-size: 10px">
          <span>
            <el-col>
              本项目为重构项目，仅为学习使用
            </el-col>
          </span>
        </el-row>
        <el-row type="flex" justify="center" align="middle" style="font-size: 10px">
          <span>
            <el-col>
              作者：jyf
            </el-col>
          </span>
        </el-row>
        <el-row type="flex" justify="center" align="middle" style="font-size: 10px">
          <span>
            <el-col>
              如有疑问，请联系作者邮箱
            </el-col>
          </span>
        </el-row>
      </el-footer>

<!--      修改头像对话框-->
      <el-dialog title="修改头像" :visible.sync="changePortraitVisible" width="30%" @close="resetPortraitChangeForm">
        <el-row style="width: 95%">
          <el-upload
            ref="uploadPortrait"
            action="http://localhost:8989/sys/upload"
            :headers="headers"
            :limit="1"
            accept=".jpeg, .JPEG, .jpg, .JPG, .png, .PNG"
            :multiple="false"
            :auto-upload="false"
            list-type="picture"
            :on-success="handleSuccess"
          >
            <el-button slot="trigger" size="small" type="primary">选择文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="uploadPortrait">确认</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
          </el-upload>
        </el-row>
      </el-dialog>

<!--      修改密码对话框-->
      <el-dialog title="修改密码" :visible.sync="changePasswordVisible" width="30%" @close="resetPasswordChangeForm">
        <el-form :model="passwordChangeForm" ref="passwordChangeForm" :rules="passwordChangeRules" label-width="140px" style="width: 95%;">
          <el-row type="flex" justify="center">
            <el-col>
              <el-form-item label="请输入原密码" prop="oldPassword">
                <el-input v-model="passwordChangeForm.oldPassword" :show-password="true" placeholder="请输入原密码"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col>
              <el-form-item label="请输入新密码" prop="newPassword">
                <el-input v-model="passwordChangeForm.newPassword" :show-password="true" placeholder="请输入新密码"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col>
              <el-form-item label="请再次输入新密码" prop="checkNewPassword">
                <el-input v-model="passwordChangeForm.checkNewPassword" :show-password="true" placeholder="请再次输入新密码"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row type="flex" justify="center">
            <el-col>
              <el-form-item>
                <el-button type="primary" @click="submitPasswordChangeForm('passwordChangeForm')">提交</el-button>
                <el-button @click="resetPasswordChangeForm">取消</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-dialog>

    </el-container>
  </div>
</template>

<script>

import Main from "./Main";
import StudentList from "./StudentList";
import TeacherList from "./TeacherList";
import ClassList from "./ClassList";
import GradeList from "./GradeList";
import AdminList from "./AdminList";

export default {
  name: "dashboard",
  components: {
    Main, StudentList, TeacherList, ClassList, GradeList, AdminList
  },
  data() {

    let checkPassword = (rule, value, callback) => {
      if (value !== this.passwordChangeForm.newPassword) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };

    return {
      headers: {
        token: localStorage.getItem("token")
      },
      id: localStorage.getItem("id"),
      role: localStorage.getItem("role"),

      changePortraitVisible: false,
      newPortrait: "",

      changePasswordVisible: false,
      passwordChangeForm: {
        oldPassword: "",
        newPassword: "",
        checkNewPassword: "",
      },
      passwordChangeRules: {
        oldPassword: [
          {required: true, message: "请输入原密码", trigger: "blur"}
        ],
        newPassword: [
          {required: true, message: "请输入新密码", trigger: "blur"}
        ],
        checkNewPassword: [
          {required: true, message: "请再次输入新密码", trigger: "blur"},
          {validator: checkPassword, trigger: "blur"}
        ]
      },

      name: localStorage.getItem("name"),
      currentView: 'Main',
      portrait: 'http://localhost:8989' + localStorage.getItem("portrait")
    }
  },
  methods: {
    switchView(path) {
      this.currentView = path;
    },

    errorHandler() {
      return true;
    },

    //权限控制
    authorization(){

    },

    handleCommand(command) {
      if (command === "changePortrait") {
        this.changePortrait();
      } else if (command === "changePassword") {
        this.changePassword();
      } else if (command === "quit") {
        this.logout();
      }
    },

    //修改头像
    changePortrait() {
      this.changePortraitVisible = true;
    },
    resetPortraitChangeForm(){
      this.changePortraitVisible = false;
      this.$refs["uploadPortrait"].clearFiles();
    },
    removeOldPortrait(oldPortraitPath) {
      this.$http.delete("http://localhost:8989/sys/deletePortrait?portraitPath=" + oldPortraitPath, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.newPortrait = '';
          this.$refs.uploadPortrait.clearFiles();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },
    uploadPortrait(){
      this.$refs["uploadPortrait"].submit();
    },
    handleSuccess(response) {
      this.resetPortraitChangeForm();
      if(response.success){
        this.newPortrait = response.portraitPath;
        this.removeOldPortrait(localStorage.getItem("portrait"));
        localStorage.setItem("portrait", this.newPortrait);
        this.$http.post("http://localhost:8989/sys/changePortrait/" + this.role + "/" + this.id, {
          newPortraitPath: this.newPortrait
        }, {
          headers: this.headers
        }).then(data => {
          if(data.data.success){
            this.$message.success(data.data.msg);
          }else{
            this.$message.error(data.data.msg);
          }
        });
        this.newPortrait = '';
        this.portrait = 'http://localhost:8989' + this.newPortrait;
      }else {
        this.$message({
          message: response.msg,
          type: 'error'
        })
      }
    },

    //修改密码
    changePassword() {
      this.changePasswordVisible = true;
    },
    resetPasswordChangeForm() {
      this.changePasswordVisible = false;
      this.$refs["passwordChangeForm"].clearValidate();
      this.passwordChangeForm = {
        oldPassword: "",
        newPassword: "",
        checkNewPassword: "",
      };
    },
    submitPasswordChangeForm(formName){
      this.$refs[formName].validate((valid) => {
        if(valid){
          this.$http.post("http://localhost:8989/sys/changePassword/" + this.role + "/" + this.id, this.passwordChangeForm, {
            headers: this.headers,
          }).then(data => {
            if (data.data.success){
              this.$message({
                message: data.data.msg,
                type: 'success'
              });
              this.changePasswordVisible = false;
              this.logout();
            }else {
              this.$message({
                message: data.data.msg,
                type: 'error'
              });
            }
          })
        }else{
          return false;
        }
      })
    },

    logout() {
      localStorage.clear();
      this.$router.push("/");
    },

  },
  created() {
  }

}
</script>

<style scoped>
</style>



