<template>
  <div>
    <el-container>
      <el-header style="font-size: 12px" height="40px">

        <el-row :gutter="10" type="flex" align="middle">
          <el-col :span="2" v-if="['admin'].includes(this.role)">
            <el-button
              type="primary"
              icon="el-icon-circle-plus"
              size="medium"
              @click="addNewClassInfo"
            >添加
            </el-button>
          </el-col>

          <el-col :span="3">
            <el-select v-model="coordinator" clearable placeholder="请选择班主任" size="medium"
                       @change="changePage">
              <el-option
                v-for="teacher in teacherList"
                :key="teacher.id"
                :label="teacher.name"
                :value="teacher.name">
              </el-option>
            </el-select>
          </el-col>

          <el-col :span="3">
            <el-select v-model="gradeName" clearable placeholder="请选择年级" size="medium"
                       @change="changePage">
              <el-option
                v-for="grade in gradeList"
                :key="grade.id"
                :label="grade.name"
                :value="grade.name">
              </el-option>
            </el-select>
          </el-col>

          <el-col :span="2">
            <el-button
              icon="el-icon-search"
              size="medium"
              @click="filtrateClazz"
            >搜索
            </el-button>
          </el-col>

        </el-row>

      </el-header>

      <el-main style="height: 725px; width: 100%">
        <!--        展示表格-->
        <el-table :data="clazzList" :stripe="true" :border="true">
          <el-table-column prop="name" label="班级" width="120">
          </el-table-column>
          <el-table-column prop="number" label="人数" width="120">
          </el-table-column>
          <el-table-column prop="coordinator" label="班主任" width="140">
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="180">
          </el-table-column>
          <el-table-column prop="telephone" label="电话" width="120">
          </el-table-column>
          <el-table-column prop="gradeName" label="年级" width="120">
          </el-table-column>
          <el-table-column prop="introduction" label="介绍">
          </el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editClazzInfo(scope.row)"
                :disabled="available()"
              >编辑
              </el-button>

              <el-popconfirm
                title="确定删除该条记录吗？"
                confirm-button-type="danger"
                @confirm="handleDelete(scope.row)"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  slot="reference"
                  :disabled="available()"
                >删除
                </el-button>
              </el-popconfirm>

            </template>
          </el-table-column>
        </el-table>
      </el-main>

      <el-footer height="15px">
        <el-row type="flex" align="bottom" justify="center">
          <el-pagination
            background
            @size-change="changePage"
            @current-change="changePage"
            :page-count="5"
            :current-page.sync='curPage'
            :page-sizes="[5, 10, 12]"
            :page-size.sync='pageSize'
            layout="total, sizes, prev, pager, next, jumper"
            :total=this.totalNum>
          </el-pagination>
        </el-row>
      </el-footer>

    </el-container>
    <!--    添加或修改表单-->
    <el-dialog :title="dialogTitle" :visible.sync="dialogTableVisible" @closed="resetClassInfo" width="30%">
      <el-form :model="clazzInfo" :rules="rules" ref="clazzInfo" label-width="60px" size="medium">
        <el-row :gutter="1">
          <el-col>

            <el-row>
              <el-col>
                <el-form-item label="班级" prop="name">
                  <el-input v-model="clazzInfo.name" placeholder="请输入班级"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="人数" prop="number">
                  <el-input v-model.number="clazzInfo.number" placeholder="请输入班级人数"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="班主任" prop="clazzName">
                  <el-select v-model="clazzInfo.coordinator" clearable>
                    <el-option
                      v-for="teacher in teacherList"
                      :key="teacher.id"
                      :label="teacher.name"
                      :value="teacher.name"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="邮箱" prop="email">
                  <el-input v-model="clazzInfo.email" placeholder="请输入邮箱"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="电话" prop="telephone">
                  <el-input v-model="clazzInfo.telephone" placeholder="请输入电话"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="介绍" prop="introduction">
                  <el-input v-model="clazzInfo.introduction" placeholder="请输入班级介绍" type="textarea"
                            :rows="3"></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item label="年级" prop="gradeName">
                  <el-select v-model="clazzInfo.gradeName" clearable>
                    <el-option
                      v-for="grade in gradeList"
                      :key="grade.id"
                      :label="grade.name"
                      :value="grade.name"
                    >
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

          </el-col>

        </el-row>
        <el-row type="flex" justify="center">
          <el-form-item>
            <el-button type="primary" @click="submitForm('clazzInfo')">提交</el-button>
            <el-button @click="closeDialog">取消</el-button>
          </el-form-item>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ClassList",
  data() {
    return {
      headers: {
        token: localStorage.getItem("token")
      },
      id: localStorage.getItem("id"),
      role: localStorage.getItem("role"),

      coordinator: "",
      teacherList: [],
      gradeName: "",
      gradeList: [],
      clazzList: [],
      clazzName: '',
      curPage: 1,
      totalNum: 0,
      pageSize: 10,
      dialogTableVisible: false,
      dialogTitle: '',
      clazzInfo: {
        name: '',
        number: 0,
        coordinator: '',
        email: '',
        telephone: '',
        gradeName: '',
        introduction: '',
      },
      rules: {
        name: [
          {required: true, message: '请输入班级名', trigger: 'blur'}
        ],
        coordinator: [
          {required: true, message: '请选择班主任', trigger: 'blur'}
        ],
        gradeName: [
          {required: true, message: '请选择年级', trigger: 'blur'}
        ],
        number: [
          {type: 'number', required: false, min: 0, message: '请输入数字', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    //授权
    available(){
      if(['admin'].includes(this.role)){
        return false;
      }
      return true;
    },

    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid){
          if (this.dialogTitle === "添加新班级信息"){
            this.submitNewClazzInfo();
          }else if (this.dialogTitle === "编辑班级信息"){
            this.updateClazzInfo();
          }
        }else {
          return false;
        }
      })
    },

    //更新班级信息
    updateClazzInfo(){
      this.$http.post("http://localhost:8989/clazz/updateInfo", this.clazzInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectClassListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //编辑班级信息
    editClazzInfo(row) {
      this.dialogTitle = "编辑班级信息";
      this.$http.get("http://localhost:8989/clazz/selectById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.clazzInfo = data.data.clazzInfo;
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
      this.dialogTableVisible = true;
    },

    //添加班级信息
    submitNewClazzInfo(){
      this.$http.post("http://localhost:8989/clazz/insert", this.clazzInfo, {
        headers: this.headers
      }).then(data => {
        if (data.data.success){
          this.$message({
            message: data.data.msg,
            type: 'success'
          });
          this.dialogTableVisible = false;
          this.selectClassListByPage();
        }else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //关闭表单
    closeDialog(){
      this.dialogTableVisible = false;
      this.resetClassInfo();
      this.$refs['clazzInfo'].clearValidate();
    },

    //重置表单信息
    resetClassInfo() {
      this.dialogTitle = "";

      this.clazzInfo = {
        name: '',
        number: '',
        coordinator: '',
        email: '',
        telephone: '',
        gradeName: '',
        introduction: '',
      }
    },

    //添加新班级
    addNewClassInfo() {
      this.dialogTitle = "添加新班级信息";
      this.dialogTableVisible = true;
    },

    //切换页面
    changePage(){
      if (this.className !== '' || this.clazzName !== ''){
        this.filtrateClazz();
      }else {
        this.selectClassListByPage();
      }
    },

    //班级分页查询
    selectClassListByPage() {
      this.$http.get("http://localhost:8989/clazz/selectClazzListByPage/" + this.curPage + "/" + this.pageSize, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.totalNum = data.data.totalNum;
          this.clazzList = data.data.clazzList;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //获取班主任列表
    selectTeacherList() {
      this.$http.get("http://localhost:8989/teacher/getTeacherNames", {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.teacherList = data.data.teacherNames;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },

    //获取年级列表
    selectGradeList(){
      this.$http.get("http://localhost:8989/grade/getGradeNames", {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.gradeList = data.data.gradeNames;
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    },


    //对班级按班主任和年级查询
    filtrateClazz() {
      if (this.gradeNameName !== '' || this.coordinator !== '') {
        this.$http.get("http://localhost:8989/clazz/filtrateClazzListByPage/" + this.curPage + "/" + this.pageSize + "?gradeName=" + this.gradeName + "&coordinator=" + this.coordinator, {
          headers: this.headers
        }).then(data => {
          if (data.data.success) {
            this.clazzList = data.data.clazzList;
            this.totalNum = data.data.totalNum;
          } else {
            this.$message({
              message: data.data.msg,
              type: 'error'
            })
          }
        })
      } else {
        this.selectClassListByPage();
      }
    },

    //删除班级信息
    handleDelete(row) {
      this.$http.delete("http://localhost:8989/clazz/deleteById/" + row.id, {
        headers: this.headers
      }).then(data => {
        if (data.data.success) {
          this.$message({
            message: data.data.msg,
            type: 'success'
          })
          this.selectClassListByPage();
        } else {
          this.$message({
            message: data.data.msg,
            type: 'error'
          })
        }
      })
    }
  },
  created() {
    this.selectTeacherList();
    this.selectGradeList();
    this.selectClassListByPage();
  }
}
</script>

<style scoped>
</style>



